.configuration-result {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.config-background::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('/pics/AIGeneratedInfrastracture.webp');
  background-size: cover;
  background-position: center;
  filter: blur(8px);
  z-index: -2; /* Фон на заднем плане */
}

.config-background::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: -1; /* Затемнение поверх фона */
}

.category {
  margin-bottom: 40px;
  width: 80%; /* Увеличение ширины категории */
  text-align: center; /* Центрирование текста */
}

.category h2 {
  font-size: 28px;
  margin-bottom: 20px;
}

.category-title {
  font-size: 24px;
  margin-bottom: 20px;
  color: #FFFFFF; /* Белый цвет текста */
}

.product-card {
  font-size: 20px;
  color: #000000;
  width: 300px;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 16px;
  margin: 16px auto; /* Центрирование карточки */
  text-align: center;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  background-color: rgba(255, 255, 255, 0.6);
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.product-image {
  width: 100%;
  height: 200px; /* Фиксированная высота */
  object-fit: contain; /* Сохранение пропорций */
  border-bottom: 1px solid #ddd;
  margin-bottom: 16px;
}

.specs {
  list-style-type: none;
  padding: 0;
}

.specs li {
  margin: 4px 0;
}

.details-button {
  display: inline-block;
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  text-decoration: none;
  border-radius: 4px;
  margin-top: 16px;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.details-button:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

/* Новый стиль для фиксированной кнопки */
.back-button {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 10px 20px;
  background-color: #61dafb;
  color: rgb(0, 0, 0);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  z-index: 1000; /* Чтобы кнопка была выше всех элементов */
}

.back-button:hover {
  background-color: #21a1f1;
  transform: scale(1.05);
}
